@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400&family=Roboto&display=swap);
* {
  box-sizing: border-box;
}

div.container-fluid {
  background-color: #fff7e0;
  min-height: 100vh;
  padding-left: 0px;
  padding-right: 0px;
}

div.row {
  margin-left: 0;
  margin-right: 0px
}

#right-align-btn-col {
  padding: 0 3rem;
  display: flex;
  justify-content: flex-end;
}

/* ~ TEXT ~ */
h1, h2, h3, h2, h5, h6{
  font-family: 'Raleway', sans-serif;
}

h1{
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
  font-weight: 100;
}

h3 {
  font-size: 2rem;
  font-weight: 200;
}

h4 {
  font-size: 1.75rem;
  font-weight: 400;
}

h5{
  font-size: 1.5rem;
}

h6 {
  font-size: 1.25rem;
}

p {
  font-family: 'Roboto', sans-serif;
  font-size: 1.5rem;
}


#sidenav-row {
  position: fixed;
  top: 3%;
  right: 5%;
  z-index: 11;
}

#sidenav-btn {
  display: flex;
  justify-content: flex-end;
}

#sidenav-btn span{
  font-size: xx-large
}

/* The side navigation menu */
.sidenav {
  height: 100%; /* 100% Full-height */
  width: 250px; /* 0 width - change this with JavaScript */
  position: fixed; /* Stay in place */
  z-index: 11; /* Stay on top */
  top: 0; /* Stay at the top */
  right: 0;
  background-color: #F9CEC7; /* Light Pink*/
  overflow-x: hidden; /* Disable horizontal l */
  padding-top: 60px; /* Place content 60px from the top */
  transition: margin-right 0.5s ease-in; /* 0.5 second transition effect to slide in the sidenav */
}

/* The navigation menu links */
.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-weight: 200;
  font-size: 25px;
  color:black;
  display: block;
  transition: 0.3s;
  display: flex;
  align-self: center;
  
}

/* When you mouse over the navigation links, change their color */
.sidenav a:hover {
  color: #BF3C1F;
}

.sidenav a:active {
  color: #BF3C1F;
}

/* Position and style the close button (top right corner) */
.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 3%;
  font-size: 2rem;
  margin-left: 50px;
  color: black;
  background: none;
  border: none;
}


/* ~ HOME ~ */
  #home {
    background: url(/static/media/home-wave.ab47f495.svg);
    background-position: 65rem -7rem;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-size: inherit;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  #home-text-col {
    margin-left: 3rem;
  }

/* ~ ABOUT ~ */ 
  #about {
    background-image: url(/static/media/about-wave.d86f3c07.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  #headshot-col {
    display: flex;
    justify-content: flex-end;
  }
  #headshot {
    margin-bottom: 1rem;
    height: auto; 
    width: 75%;
  }
  #about-text-col{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 2rem;
  }
  #about-text-col h1 {
    display: flex;
    width: 20rem;
  }
  #see-my-work-btn {
    display: flex;
    align-self: flex-start;
  }

/* ~ SKILLS ~ */
  #skills {
    margin-top: 15rem;
  }
  .skills-block-row {
    justify-content: center;
    margin: 0 4rem 0 4rem;
  }
  #skills-to-resume-btn-row {
    margin: 2rem 0 0 0;
  }

/* ~ CONTACT ~ */
  #contact {
    margin-bottom: 0;
  }
  #contact-info-col {
    display: flex;
    justify-content: center;
  }
  #contact-info-col svg {
    height: 85vh;
  }
  


section {
  min-height: 100vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
  background-color: #fff7e0;
}


.section-header {
  margin: 1rem 0 1rem 2rem;
  height: 5rem;
  display: flex;
  align-items: center;
}
a.btn {
  border-radius: 50px;
  background-color: #F9CEC7;
  color: black;
  text-decoration: none;
  padding: 1rem 2rem 1rem 2rem;
}

#right-align-btn-col {
  padding: 0 3rem;
  display: flex;
  justify-content: flex-end;
}



.card.skills-block {
  background: #fff7e0;
  border: 3px solid #BF3C1F;
  width: 100%; 
  height: 37rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 .5rem 0 1.5rem;
}
.card.skills-block p {
  font-size: 1.25em;
}

#resume-right-col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 1rem;
}
.row.resume-row {
  margin: 1rem 2rem 1rem 2rem;
}
.resume-subrow {
  margin-bottom: 1rem;
}
.resume-subcol {
  padding: 0;
}

.resume-section-header {
  margin-bottom: 1rem;
}
.resume-block {
  background: #BF3C1F;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  height: 100%;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#resume {
  padding-bottom: 1rem;
}

#resume .section-header {
  height: 3rem;
  align-items: flex-end;
}
#resume h5, #resume h6{
  font-weight: bold;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
}
#resume p {
  font-size: 1rem;
  margin-bottom: .5rem;
}
.resume-block li, .resume-skills-block li {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  margin-bottom: .5rem;
}
#resume-right-col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 1rem;
}
#resume-education-block {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
#resume-download-btn {
  float: right;
}
#file-admin-block {
  margin-top: 1rem;
}
.date {
  float: right;
  margin-right: 1rem;
}

div.text-white.card {
  border: none;
}

div.card-img-overlay {
  padding: 0rem;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}

img.card-img {
  border-radius: 0;
  border-radius: initial;
}

.portfolio-card-title { 
  background: #BF3C1F;
  height: 3rem;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 1rem;
  text-align: right;
  font: 1.5rem;
}

#portfolio .col {
  padding-left: 0px;
  padding-right: 0px;
}
#portfolio-content-row {
  height: 100vh;
  margin-bottom: 5rem;
}
#portfolio-content-row .col {
  display: flex;
  flex-direction: column;
  justify-items: center;
}
.text-white.project-card.card {
  max-width: 25%;
  margin: .5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: none;
}



#project-content-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  min-height: 80vh;
}

#ofs .text-white.project-card.card {
  max-width: 33%;
}


#rest .text-white.project-card.card {
  max-width: 20%;
}

.modal-photo {
  max-height: 85vh;
}

.modal-dialog{
  min-width: 90vw
}

#modal-img-col {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#modal-btn-col {
  display: flex; 
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#modal-btn {
  width: 2rem;
  height: 4rem;
  font-size: 1rem;
  font-weight: 900;
  display: flex;
  align-items: center;
  justify-content: center;
}


/* ~ MEDIA QUERIES ~ */
@media screen and (max-width: 991px) {
  /* ~ GENERAL ~ */
  h1 {
    font-size: 2.5rem
  }
  p {
    font-size: 1.3rem
  }

  /* ~ ABOUT ~ */
  #headshot-col {
    justify-content: center;
  }
  #headshot {
    height: 50vh;
    width: auto;
  }
  #about-col {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  #about-col p {
    max-width: 50vw;
    text-align: center;
  }
  /* ~ RESUME ~ */
  .date {
    float: left;
  }
} 
@media screen and (max-width: 767px) {
  /* ~ GENERAL ~ */
  .section-header {
    margin: 2rem 0 0 1rem;
  }
  /* ~ HOME ~ */ 
  #home-col {
    margin-left: 2rem;
  }
  /* ~ ABOUT ~ */
  #about-text-col h1 {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  #see-my-work-btn {
    align-self: center;
  }
  /* ~ RESUME ~ */
  .row.resume-row {
    margin: .5rem;
  }
  .resume-block {
    margin-top: 1rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  .resume-section-header {
    margin: 1rem 0 0 1rem;
  }
  #my-skills, #my-experience {
    margin: 1rem;
  }
}
@media screen and (max-width: 700px) {
  /* ~ WAVE ~ */
  .home-wave, .about-wave {
    right: -8rem;
  }
  /* ~ SKILLS ~ */
  #skills-block-row {
    margin: 0;
    flex-direction: column;
  }
  .skills-block {
    height: 15rem;
    margin-bottom: 2rem;
  }
  #skills-to-resume-btn-row {
    margin: 0;
  }
}

@media screen and (max-width: 580px) {
  /* ~ WAVE ~ */
  .home-wave, .about-wave {
    right: -10rem;
  }
  /* ~ ABOUT ~ */
  #about-col p {
    max-width: 75vw;
  }
}
@media screen and (max-width: 550px) {
  /* ~ HOME ~*/
  #home-col {
    margin-left: 1rem;
  }
  /* ~ CONTACT ~ */
  #orange {
    transform: scale(1.2) translate(-29% , 15%);
  }
  #orange text {
    transform: translate(-5%, 2%);
  }
  #pink {
    transform: scale(1.2) translate(-3%, -30%);
  }
  #white {
    transform: scale(1.2) translate(14%, -25%)
  }
  #white text {
    transform: translateY(2%)
  }


}
@media screen and (max-width: 480px) {
  .btn {
    font-size: .85rem;
  }
} 
/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
  /* ~ SIDENAV ~*/
  .sidenav {
    padding-top: 15px;
  }
  .sidenav a {
    font-size: 18px;
  }
}

@media screen and (max-width: 450px) {
   /* ~ GENERAL ~*/
  section {
    margin-bottom: 90px;
  }
  #right-align-btn-col {
    justify-content: center;
  }
  
}

@media screen and (max-width: 400px) {
  /* ~ CONTACT ~ */
  #orange {
    transform: scale(1.5) translate(-35% , 15%);
  }
  #orange text {
    transform: translateX(-5%);
  }
  #pink {
    transform: scale(1.5) translate(-9%, -50%);
  }
  #white {
    transform: scale(1.5) translate(8%, -38%)
  }
  #white text {
    transform: translateY(-1%)
  }
}



